// components
import Header from "../../WelcomePages/components/Header";
import BlogList from "../../../Components/Blogs/Blogs/BlogsList";
import BannerBottom from "../BannerBottom";
import Footer from "../../WelcomePages/components/Footer";

// react-helmet
import { Helmet } from "react-helmet";

const BlogsList = () => {
  return (
    <div>
      <Helmet>
        <title>Blogs by EasySocial</title>
      </Helmet>
      <Header />
      <BlogList />
      <BannerBottom />
      <Footer />
    </div>
  );
};

export default BlogsList;
