import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "banner-bottom": {
    backgroundColor: "#12B0EE",
    display: "flex",
    justifyContent: "center",
    height: "410px",
    padding: "70px 28px 75px",
    position: "relative",
    textAlign: "center",
    zIndex: 99,

    "& .content": {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      justifyContent: "space-between",
    },

    "& h4": {
      color: "#ffffff",
      fontSize: "30px",
      fontWeight: 900,
      lineHeight: "32px",
    },

    "& .slider-container": {
      margin: "0 auto",
      maxWidth: "100vw",

      "& .swiper-slide": {
        alignSelf: "center",
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {
    "banner-bottom": {
      padding: "60px 0 60px",
      height: "350px",

      "& h4": {
        fontSize: "36px",
        lineHeight: "42px",
      },

      "& .slider-container": {
        maxWidth: "980px",
      },
    },
  },
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
